.card-item{display:flex;flex-direction:column;align-items:center;border:1px solid #000;border-radius:5px;box-shadow:0 15px 15px rgba(255,255,255,.25),0 15px 15px rgba(255,255,255,.2);transition:.2s}.card-item:hover{transform:scale(1.005);box-shadow:0 15px 30px rgba(0,0,0,.25),0 15px 15px rgba(0,0,0,.2)}.about-container{display:flex;justify-content:space-between;padding:5px;width:290px}.card-item__image{border-radius:5px}.checkbox{display:block}.checkbox input{display:none}.checkbox input+span:before{content:"♥";font-size:40px;color:gray;cursor:pointer}.checkbox input:checked+span:before{content:"♥";color:red}